.weather-interface {
    margin: 4vmin;
    display: flex;
    flex-direction: column;
    background: radial-gradient(ellipse at center, #0d1d31ed 0%, #0c0d13dd 100%);
    border-radius: 3vmin;
    padding-left: 2vmin;
    padding-right: 2vmin;
  }
  
  .weather-details {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* Adjust the number of columns as per your design */
    gap: 10px; /* Adjust the gap between the grid items */
  }
  
  .weather-box,
  .temp-box,
  .high-low-box,
  .wind-speed-box {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid #ffffff; /* White border with 2px width */
    border-radius: 3vmin;
    background-color: transparent; /* Transparent background */
    margin: 1vmin;
  }

  .spacing {
    height: 14vmin;
  }